<template>
  <div class="container">
    <form class="form" @submit.prevent>
      <div class="input-field">
        <label for="name" class="materialize-label active">Name</label>
        <input
          type="text"
          id="name"
          class="materialize-input"
          v-model.trim="newExercise.name"
        />
      </div>
      <div class="input-field">
        <label for="adjustments" class="materialize-label active"
          >Einstellungen</label
        >
        <input
          type="text"
          id="adjustments"
          class="materialize-input"
          v-model.trim="newExercise.adjustments"
        />
      </div>
    </form>
    <p class="error" v-if="error">{{ error }}</p>
    <div class="fixed-action-btn">
      <a
        @click="save"
        class="btn-floating btn-large waves-effect waves-light green darken-4"
        ><i class="fas fa-check"></i
      ></a>
    </div>
    <div class="fixed-action-btn fab-left">
      <a
        @click="$emit('close')"
        class="btn-floating btn-large waves-effect waves-light red darken-4"
        ><i class="fas fa-arrow-left"></i
      ></a>
    </div>
  </div>
</template>

<script>
import Service from "../Service";

export default {
  name: "CreateNewExercise",
  data() {
    return {
      newExercise: {
        name: "",
        adjustments: ""
      },
      error: ""
    };
  },
  methods: {
    async save() {
      try {
        await Service.insertExercise(this.newExercise);
        this.$emit("saved");
        this.$emit("close");
      } catch (err) {
        this.error = err.message;
      }
    }
  }
};
</script>

<style scoped>
div.container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: white;
  padding: 20px;
}

div.input-field {
  margin-top: 50px;
}

div.fab-left {
  right: auto;
  margin-left: 3px;
}

p.error {
  border: 1px solid red;
  background-color: orange;
  padding: 10px;
  margin-bottom: 15px;
}
</style>
