<template>
  <div class="container">
    <div class="modal__wrapper-inner">
      <p>Neuen Durchgang speichern</p>
      <input type="text" class="heading" v-model.trim="update.name" />
      <form class="form" @submit.prevent>
        <div class="input-field">
          <label for="adj" class="active label-adj">Einstellungen</label>
          <input
            type="text"
            id="adj"
            class="materialize-input"
            v-model.trim="update.adjustments"
          />
        </div>

        <div class="set light-blue lighten-4">
          <h6>Set 1</h6>
          <div class="row">
            <label for="Rep1" class="label col s3">Wdh</label>
            <input
              type="text"
              id="Rep1"
              class="input col s6"
              v-model.trim="update.progress.repetitions_1"
            />
          </div>
          <div class="row">
            <label for="wgh1" class="label col s3">Gew</label>
            <input
              type="text"
              id="wgh1"
              class="input col s6"
              v-model.trim="update.progress.weight_1"
            />
          </div>
        </div>
        <div class="set light-blue lighten-4">
          <h6>Set 2</h6>
          <div class="row">
            <label for="Rep2" class="label col s3">Wdh</label>
            <input
              type="text"
              id="Rep2"
              class="input col s6"
              v-model.trim="update.progress.repetitions_2"
            />
          </div>
          <div class="row">
            <label for="wgh2" class="label col s3">Gew</label>
            <input
              type="text"
              id="wgh2"
              class="input col s6"
              v-model.trim="update.progress.weight_2"
            />
          </div>
        </div>
        <div class="set light-blue lighten-4">
          <h6>Set 3</h6>
          <div class="row">
            <label for="Rep3" class="label col s3">Wdh</label>
            <input
              type="text"
              id="Rep3"
              class="input col s6"
              v-model.trim="update.progress.repetitions_3"
            />
          </div>
          <div class="row">
            <label for="wgh3" class="label col s3">Gew</label>
            <input
              type="text"
              id="wgh3"
              class="input col s6"
              v-model.trim="update.progress.weight_3"
            />
          </div>
        </div>

        <br />
        <div class="input-field">
          <label for="comment" class="active label-comment">Kommentar</label>
          <textarea
            type="text"
            id="comment"
            class="materialize-textarea"
            v-model.trim="update.progress.comment"
          />
        </div>

        <div class="fixed-action-btn">
          <a
            @click="save"
            class="btn-floating btn-large waves-effect waves-light green darken-4"
            ><i class="fas fa-check"></i
          ></a>
        </div>
        <div class="fixed-action-btn fab-left">
          <a
            @click="$emit('close')"
            class="btn-floating btn-large waves-effect waves-light red darken-4"
            ><i class="fas fa-arrow-left"></i
          ></a>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import Service from "../Service";

export default {
  name: "Exercise",
  props: ["exercise"],
  data() {
    return {
      update: {
        name: "",
        adjustments: "",
        progress: {
          // initial values are a copy of latest progress item using destructuring in created hook
          // repetitions_1: "",
          // weight_1: "",
          // repetitions_2: "",
          // weight_2: "",
          // repetitions_3: "",
          // weight_3: "",
          // comment: "",
          // date: ""
        }
      }
    };
  },
  created() {
    const sortedProgress = this.exercise.progress.sort(
      (a, b) => new Date(b.date) - new Date(a.date)
    );
    this.update.progress = { ...sortedProgress[0] };
    this.update.name = this.exercise.name;
    this.update.adjustments = this.exercise.adjustments;
    console.log("exercise: ", this.exercise);
    console.log("latest progress: ", this.update.progress);
  },
  methods: {
    async save() {
      try {
        const update = {
          name: this.update.name,
          adjustments: this.update.adjustments,
          repetitions_1: this.update.progress.repetitions_1,
          weight_1: this.update.progress.weight_1,
          repetitions_2: this.update.progress.repetitions_2,
          weight_2: this.update.progress.weight_2,
          repetitions_3: this.update.progress.repetitions_3,
          weight_3: this.update.progress.weight_3,
          comment: this.update.progress.comment,
          date: new Date()
        };
        const updated = await Service.updateExercise(this.exercise._id, {
          ...update
        });
        console.log("updated: ", updated);
        this.$emit("saved");
        this.$emit("close");
      } catch (err) {
        this.error = err.message;
      }
    }
  }
};
</script>

<style scoped>
div.container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: white;
  padding: 20px;
}

div.modal__wrapper-inner {
  height: 100%;
  width: 100%;
  overflow: auto;
}

input.heading {
  padding: 5px 0;
  font-size: 1.3rem;
  font-weight: 700;
  text-align: center;
  border: none;
  height: 2rem;
}

form.form {
  display: flex;
  flex-direction: column;
  align-content: center;
}
div.set {
  margin-top: 5px;
  border: 1px solid #333;
  border-radius: 5px;
  padding: 5px 0;
}
div.row {
  margin: 0;
}

div.input-field {
  margin: 5px 0;
}

input.input,
label.label {
  width: 100px;
  height: 30px;
  font-size: 18px;
  text-align: left;
  justify-self: center;
  color: #333;
}

input.input {
  border: 1px solid #333;
  border-radius: 5px;
  background: #fff;
  text-align: center;
  font-weight: 700;
}

label.label {
  display: flex;
  align-items: center;
}

input.materialize-input {
  height: 2rem;
}

textarea.materialize-textarea {
  padding-top: 1rem;
  min-height: 4rem;
}

div.fab-left {
  right: auto;
  margin-left: 3px;
}
</style>
